
.head{height: 30px;padding-top: 30px;width:100%;position: fixed;top:0;left: 0;z-index: 50;background: #eb413d url("../assets/image/h.jpg") no-repeat left top;background-size:100%;
}
.head p{line-height: 26px;font-size: 16px;text-align: left;height: 26px;color: white;margin-left: 30px;
}
.head .img1{position: absolute;right:60px;top: 30px;width: 25px;
}
.head .img2{position: absolute;right:10px;top: 30px;width: 35px;
}
.head .img3{position: absolute;left:10px;top: 30px;width: 40px;
}



