
.l-main .mid .tab{background: white;height: 40px;padding: 10px 40px
}
.l-main .mid .tab .left{float: left;text-align: left;width: 30%;height: 40px;
}
.l-main .mid .tab .right{float: left;text-align: left;width: 60%;overflow: hidden;height: 40px;position: relative;
}
.l-main .mid .tab h3{font-weight: normal;height: 20px;line-height: 20px;font-size: 14px;
}
.l-main .mid .tab p{height: 20px;line-height: 20px;font-size: 12px;color: #a3a6aa;
}
.l-main .mid .tab span{color: #d9d9d9;line-height: 40px;float: left;margin:0 10px;
}
.l-main .mid .tab .right ul{position: absolute;top:0;left: 0;-webkit-animation: list 10s linear infinite;animation: list 10s linear infinite;
}
@-webkit-keyframes list {
0%{top: 0;
}
100%{top:-220px;
}
}
@keyframes list {
0%{top: 0;
}
100%{top:-220px;
}
}
.l-main .mid .tab .right li{height: 40px;line-height: 20px;font-size: 14px;color: #666666;width: 100%;
}
.l-main .mid .pic{height: 156px;width: 100%;margin-bottom: 5px;
}
.l-main .mid .pic img{height: 156px;
}



