
.m-head{background: #eb413d url("../../assets/image/h.jpg") no-repeat left top;background-size:100%;height: 180px;padding-top: 40px;
}
.m-head h4{height: 30px;line-height: 30px;color: white;font-size: 16px;font-weight: normal;background: url("../../assets/image/m_00.jpg") no-repeat left center;float: right;margin-right: 20px;padding-left: 20px;background-size: 30%;
}
.m-head .login{padding-top: 50px;margin-bottom: 50px;
}
.m-head .login .left{float: left;height: 35px;width: 120px;background: white;color: #eb413d;text-align: center;line-height: 35px;font-size: 16px;margin-left: 20px;border-radius:5px;
}
.m-head .login span{color: white;height: 35px;line-height: 35px;font-size: 14px;float: left;margin-left: 10px;
}
.m-head .login .right{float: right;height: 35px;width: 100px;color: white;text-align: center;line-height: 35px;font-size: 16px;border-radius:45px 0 0 45px;border-top: 1px solid white;border-bottom: 1px solid white;border-left: 1px solid white;background: url("../../assets/image/m_01.jpg") no-repeat 10px center;background-size:20%; padding-left: 20px;
}
.m-head ul{display: inline-block;
}
.m-head li{float: left;color: white;font-size: 16px;height: 30px;line-height: 30px;padding-left: 30px;
}
.m-head li span{margin: 0 10px;color: #f49a98;
}
.m-head ul .li1{background: url("../../assets/image/m_03.jpg") no-repeat left center;background-size:20%;
}
.m-head ul .li2{background: url("../../assets/image/m_02.jpg") no-repeat left center;background-size:20%;
}
.m-head ul .li3{background: url("../../assets/image/m_04.jpg") no-repeat left center;background-size:20%;
}
.m-main{background: #f6f6f6;position: relative;padding-top: 40px;
}
.m-main .page1{height: 50px;background: white;width: 90%;-webkit-box-shadow: 0 0 5px rgba(0,0,0,0.5);box-shadow: 0 0 5px rgba(0,0,0,0.5);position: absolute;left: 5%;top:-25px;border-radius: 5px;
}
.m-main .page1 a{color: #aaaaaa;line-height: 50px;
}
.m-main .page1 span{color: #aaaaaa;margin: 0 35px;line-height: 50px;
}
.m-main .page2{height: 70px;background: white;width: 90%;-webkit-box-shadow: 0 0 5px rgba(0,0,0,0.5);box-shadow: 0 0 5px rgba(0,0,0,0.5);margin-left: 5%;border-radius: 5px;padding-top: 10px;
}
.m-main .page2 li{float: left;width: 24.7%;text-align: center;border-right:1px solid #e6e6e6;
}
.m-main .page2 li .pic{height: 30px;padding-top: 10px;
}
.m-main .page2 li img{width: 15px;
}
.m-main .page2 li i{height: 20px;line-height: 20px;color: #aaaaaa;font-size: 14px;
}
.m-main .page3{width: 100%;overflow:auto;padding-top: 15px;height: 252px;
}
.m-main .page3 .list1{width: 90%;background: white;border-radius: 5px;-webkit-box-shadow: 0 0 5px rgba(0,0,0,0.5);box-shadow: 0 0 5px rgba(0,0,0,0.5);margin-left: 5%;margin-bottom: 10px;
}
.m-main .page3 .list1 li{border-bottom: 1px solid #e6e6e6;height: 40px;
}
.m-main .page3 .list1 li h3{text-align: left;font-weight: normal;line-height: 40px;font-size: 14px;padding-left: 50px;
}
.m-main .page3 .list1 li .list1-h1{background: url("../../assets/image/m_09.jpg") no-repeat 15px center;background-size:5%;
}
.m-main .page3 .list1 li .list1-h2{background: url("../../assets/image/m_10.jpg") no-repeat 15px center;background-size:5%;
}
.m-main .page3 .list1 li span{float: right;line-height: 40px;font-size: 14px;color: #b5b5b5;margin-right: 15px;
}
.m-main .page3 .list1 li span i{margin-left: 10px;font-family: Consolas;font-size: 20px;
}
.m-main .page3 .list2{width: 90%;background: white;border-radius: 5px;-webkit-box-shadow: 0 0 5px rgba(0,0,0,0.5);box-shadow: 0 0 5px rgba(0,0,0,0.5);margin-left: 5%;margin-bottom: 10px;
}
