}
.m-main .page3 .list2 li h3{text-align: left;font-weight: normal;line-height: 40px;font-size: 14px;padding-left: 50px;
}
.m-main .page3 .list2 li .list2-h1{background: url("../../assets/image/m_11.jpg") no-repeat 15px center;background-size:5%;
}
