}
.m-main .page3 .list2 li .list2-h3{background: url("../../assets/image/m_13.jpg") no-repeat 15px center;background-size:5%;
}
.m-main .page3 .list2 li span{float: right;line-height: 40px;font-size: 14px;color: #b5b5b5;margin-right: 15px;
