}
.m-main .page3 .list3{width: 90%;background: white;border-radius: 5px;-webkit-box-shadow: 0 0 5px rgba(0,0,0,0.5);box-shadow: 0 0 5px rgba(0,0,0,0.5);margin-left: 5%;margin-bottom: 10px;
}
.m-main .page3 .list3 li{border-bottom: 1px solid #e6e6e6;height: 40px;
}
