}
.m-main .page3 .list3 li .list3-h1{background: url("../../assets/image/m_14.jpg") no-repeat 15px center;background-size:5%;
}
.m-main .page3 .list3 li .list3-h2{background: url("../../assets/image/m_15.jpg") no-repeat 15px center;background-size:5%;
}
.m-main .page3 .list3 li .list3-h3{background: url("../../assets/image/m_16.jpg") no-repeat 15px center;background-size:5%;
}
.m-main .page3 .list3 li .list3-h4{background: url("../../assets/image/m_17.jpg") no-repeat 15px center;background-size:5%;
}
.m-main .page3 .list3 li span{float: right;line-height: 40px;font-size: 14px;color: #b5b5b5;margin-right: 15px;
}
.m-main .page3 .list3 li span i{margin-left: 10px;font-family: Consolas;font-size: 20px;
}



