
.n-main{width: 100%;padding-top: 60px;padding-bottom: 60px;
}
.n-main .top{height: 190px;
}
.n-main .mid{width: 100%;
}
.n-main .mid .itemA{padding: 10px;border-bottom: 1px solid #d8d8d8;text-align: left;
}
.n-main .mid .itemA img{float: right;width: 100px;
}
.n-main .mid .itemA h3{font-weight: normal;line-height: 22px;font-size: 18px;width: 75%;color: #333333;margin-bottom: 10px;
}
.n-main .mid .itemA .bot{width: 75%;height: 30px;line-height: 30px;color: #b2b2b2;font-size: 14px;
}
.n-main .mid .itemA .bot span{float: right;margin-right: 10px;
}
.n-main .mid .itemA .bot .close{font-family: Consolas;font-size: 26px;-webkit-transform: rotate(45deg);transform: rotate(45deg)
}



