
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
*{margin: 0;padding: 0;
}
li{list-style: none;
}
i{font-style: normal;
}
a{text-decoration: none;color: #2c3e50;
}
img,button{border: none;
}
button{background: transparent;
}



